import React, { useEffect, useState } from 'react';
import Input from 'components/Input';
import Checkbox from 'components/Checkbox';
import {
  Head,
  SelectBase,
  Fabric,
  Label,
  FormGroup,
  Result,
  Cost,
  Calculation,
  Button,
} from 'components/Shared';
import { romeOptions, TAPE_PRICE, TAPE_COEF, ROME_CORNICE_BASE_PRICE, SEWING_BASE_PRICE } from 'config';
import OrderModal from 'components/OrderModal';

import SelectColor from 'components/SelectColor';
import { barhatColors, blackoutColors, tulleColors } from '../../../colors';

const mapColors = {
  romeTulle: tulleColors,
  romeBlackout: blackoutColors,
  romeCloth: barhatColors,
};

const baseMap = romeOptions.reduce((res, i) => {
  res[i.id] = i.title;
  return res;
}, {});

const Rome = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);
  const [color, setColor] = useState('');
  const [category, setCategory] = useState(option || null);

  const [values, setValues] = useState({
    base: option ? romeOptions.find(i => i.id === option).price : null,
    width: null,
    height: null,
    cornice: false,
    type: 'standart',
  });

  const { base, width, height, cornice } = values;

  const materialCost = +(base * width).toFixed(2);
  const sewingCost = +(SEWING_BASE_PRICE * width * height).toFixed(2);
  const tapeCost = +(width * (height / TAPE_COEF) * TAPE_PRICE).toFixed(2);
  const corniceCost = cornice ? +(width * ROME_CORNICE_BASE_PRICE).toFixed(2) : 0;

  const totalPrice = +(materialCost + sewingCost + tapeCost + corniceCost).toFixed(2);

  useEffect(() => {
    setValues({ ...values, cornice: false });
  }, [values.base]);

  return (
    <div>
      <Head>
        1. –í—ã–±–µ—Ä–∏—Ç–µ <i>–∫–∞—Ç–µ–≥–æ—Ä–∏—é</i>
      </Head>
      <SelectBase>
        {romeOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => {
              setColor('');
              setCategory(item.id);
              setValues({ ...values, base: item.price });
            }}
            active={category === item.id}
          >
            {item.title}
            <Cost>–°—Ç–æ–∏–º–æ—Å—Ç—å<br />{item.price} —Ä—É–±/–ø–æ–≥–æ–Ω–Ω—ã–π –º–µ—Ç—Ä</Cost>
          </Fabric>
        ))}
      </SelectBase>
      {base && category ? (
        <>
          <Head>
            2. –í—ã–±–µ—Ä–∏—Ç–µ <i>—Ü–≤–µ—Ç</i>
          </Head>
          <SelectColor options={mapColors[category]} select={setColor} value={color} />
        </>
      ) : null}
      {base && color ? (
        <>
          <Head>
            3. –£–∫–∞–∂–∏—Ç–µ <i>—Ä–∞–∑–º–µ—Ä—ã</i>
          </Head>
          <FormGroup>
            <Label>–î–ª–∏–Ω–∞ –∫–∞—Ä–Ω–∏–∑–∞ (—à–∏—Ä–∏–Ω–∞ —à—Ç–æ—Ä—ã)</Label>
            <br />
            <Input changed={(w) => setValues({ ...values, width: w })} placeholder="–£–∫–∞–∂–∏—Ç–µ —à–∏—Ä–∏–Ω—É –≤ –º–µ—Ç—Ä–∞—Ö" />
          </FormGroup>
          <FormGroup>
            <Label>–í—ã—Å–æ—Ç–∞ —à—Ç–æ—Ä—ã</Label>
            <br />
            <Input changed={(h) => setValues({ ...values, height: h })} placeholder="–£–∫–∞–∂–∏—Ç–µ –≤—ã—Å–æ—Ç—É –≤ –º–µ—Ç—Ä–∞—Ö" />
          </FormGroup>
          <Checkbox
            onChange={(e) => setValues({ ...values, cornice: +e.target.checked })}
            checked={cornice}
            title="–î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä–Ω–∏–∑"
          />
          {width && height ? (
            <Result>
              <Calculation>
                –ò–∑ —á–µ–≥–æ —Å–∫–ª–∞–¥—ã–≤–∞–µ—Ç—Å—è –∏—Ç–æ–≥–æ–≤–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å:
                <div>–°—Ç–æ–∏–º–æ—Å—Ç—å —Ç–∫–∞–Ω–∏: {base}‚ÇΩ * {width}–º = {materialCost}‚ÇΩ</div>
                <div>–¶–µ–Ω–∞ –ø–æ—à–∏–≤–∞: {SEWING_BASE_PRICE}‚ÇΩ * {width}–º * {height}–º = {sewingCost}‚ÇΩ</div>
                <div>
                  –¶–µ–Ω–∞ —à—Ç–æ—Ä–Ω–æ–π –ª–µ–Ω—Ç—ã:&nbsp;
                  {width}–º * ({height}–º/{TAPE_COEF}) * {TAPE_PRICE}‚ÇΩ = {tapeCost}‚ÇΩ
                </div>
                {cornice ? (
                  <div>–¶–µ–Ω–∞ –∫–∞—Ä–Ω–∏–∑–∞: {width}–º * {ROME_CORNICE_BASE_PRICE}‚ÇΩ = {corniceCost}‚ÇΩ</div>
                ) : null}
              </Calculation>
              <div>
                –ò—Ç–æ–≥–æ: {totalPrice} ‚ÇΩ
                <br />
                <Button onClick={() => toggleModal(true)}>–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑</Button>
              </div>
            </Result>
          ) : null}
          {showOrderModal ? (
            <OrderModal
              details={
                `–†–∏–º—Å–∫–∞—è —à—Ç–æ—Ä–∞ (${baseMap[category]?.toLowerCase()}), —Ü–≤–µ—Ç: ${color?.toLowerCase()}, —à–∏—Ä–∏–Ω–∞ ${width}–º,
                 –≤—ã—Å–æ—Ç–∞ ${height}–º${cornice ? ', –∫–∞—Ä–Ω–∏–∑' : ''}.`
              }
              price={totalPrice}
              close={() => toggleModal(false)}
            />
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default Rome;
