import React, { useState, useEffect } from 'react';
import Input from 'components/Input';
import Checkbox from 'components/Checkbox';
import KsInfo from 'components/KsInfo';
import { tulleOptions, TAPE_PRICE, CORNICE_REG_PRICE, SEWING_BASE_PRICE } from 'config';
import OrderModal from 'components/OrderModal';

import {
  Head,
  SelectBase,
  Fabric,
  Label,
  FormGroup,
  Result,
  Cost,
  Calculation,
  Button,
} from 'components/Shared';

import SelectColor from 'components/SelectColor';
import { barhatColors, tulleColors, crepeColors } from '../../colors';

const mapColors = {
  tulleVeil: tulleColors,
  tulleCrepe: crepeColors,
  tulleLinen: barhatColors,
};

const baseMap = tulleOptions.reduce((res, i) => {
  res[i.id] = i.title;
  return res;
}, {});

const TulleTab = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);
  const [color, setColor] = useState('');
  const [category, setCategory] = useState(option || null);

  const [values, setValues] = useState({
    base: option ? tulleOptions.find(i => i.id === option).price : null,
    width: null,
    waves: null,
    cornice: false,
  });

  const { base, width, waves, cornice } = values;

  const materialCost = +(base * width * waves).toFixed(2);
  const sewingCost = +(SEWING_BASE_PRICE * width * waves).toFixed(2);
  const tapeCost = +(width * waves * TAPE_PRICE).toFixed(2);
  const corniceCost = cornice ? +(width * CORNICE_REG_PRICE).toFixed(2) : 0;

  const totalPrice = +(materialCost + sewingCost + tapeCost + corniceCost).toFixed(2);

  useEffect(() => {
    setValues({ ...values, cornice: false });
  }, [values.base]);

  return (
    <div>
      <Head>
        1. –í—ã–±–µ—Ä–∏—Ç–µ
        &nbsp;
        <i>–º–∞—Ç–µ—Ä–∏–∞–ª</i>
      </Head>
      <SelectBase>
        {tulleOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => {
              setColor('');
              setCategory(item.id);
              setValues({ ...values, base: item.price });
            }}
            active={category === item.id}
          >
            {item.title}
            <Cost>–°—Ç–æ–∏–º–æ—Å—Ç—å<br />{item.price} —Ä—É–±/–ø–æ–≥–æ–Ω–Ω—ã–π –º–µ—Ç—Ä</Cost>
          </Fabric>
        ))}
      </SelectBase>
      {base && category ? (
        <>
          <Head>
            2. –í—ã–±–µ—Ä–∏—Ç–µ <i>—Ü–≤–µ—Ç</i>
          </Head>
          <SelectColor options={mapColors[category]} select={setColor} value={color} />
        </>
      ) : null}
      {base && color ? (
        <>
          <Head>
            2. –£–∫–∞–∂–∏—Ç–µ
            &nbsp;
            <i>—Ä–∞–∑–º–µ—Ä—ã</i>
          </Head>
          <FormGroup>
            <Label>–î–ª–∏–Ω–∞ –∫–∞—Ä–Ω–∏–∑–∞ (—à–∏—Ä–∏–Ω–∞ —à—Ç–æ—Ä—ã)</Label>
            <br />
            <Input changed={w => setValues({ ...values, width: w })} placeholder="–£–∫–∞–∂–∏—Ç–µ —à–∏—Ä–∏–Ω—É –≤ –º–µ—Ç—Ä–∞—Ö" />
          </FormGroup>
          <FormGroup>
            <Label>
              –ñ–µ–ª–∞–µ–º–∞—è —Å–∫–ª–∞–¥–∫–∞ (–∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç —Å–±–æ—Ä–∫–∏)
              <KsInfo />
            </Label>
            <br />
            <Input changed={w => setValues({ ...values, waves: w })} placeholder="–£–∫–∞–∂–∏—Ç–µ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç - –Ω–∞–ø—Ä–∏–º–µ—Ä 2.5" />
          </FormGroup>
          <Checkbox
            onChange={e => setValues({ ...values, cornice: +e.target.checked })}
            checked={cornice}
            title="–î–æ–±–∞–≤–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å–Ω—ã–π –∫–∞—Ä–Ω–∏–∑"
          />

          {width && waves ? (
            <Result>
              <Calculation>
                –ò–∑ —á–µ–≥–æ —Å–∫–ª–∞–¥—ã–≤–∞–µ—Ç—Å—è –∏—Ç–æ–≥–æ–≤–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å:
                <div>–°—Ç–æ–∏–º–æ—Å—Ç—å —Ç–∫–∞–Ω–∏: {base}‚ÇΩ * {width}–º * {waves} = {materialCost}‚ÇΩ</div>
                <div>–¶–µ–Ω–∞ –ø–æ—à–∏–≤–∞: {SEWING_BASE_PRICE} * {width}–º * {waves} = {sewingCost}‚ÇΩ</div>
                <div>
                  –¶–µ–Ω–∞ —à—Ç–æ—Ä–Ω–æ–π –ª–µ–Ω—Ç—ã:&nbsp;
                  {width}–º * {waves} * {TAPE_PRICE}‚ÇΩ = {tapeCost}‚ÇΩ
                </div>
                {cornice ? (
                  <div>–¶–µ–Ω–∞ –∫–∞—Ä–Ω–∏–∑–∞: {width}–º * {CORNICE_REG_PRICE}‚ÇΩ = {corniceCost}‚ÇΩ</div>
                ) : null}
              </Calculation>
              <div>
                –ò—Ç–æ–≥–æ: {totalPrice} ‚ÇΩ
                <br />
                <Button onClick={() => toggleModal(true)}>–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑</Button>
              </div>
            </Result>
          ) : null}

          {showOrderModal ? (
            <OrderModal
              details={
                `–¢—é–ª—å (${baseMap[category]?.toLowerCase()}), —Ü–≤–µ—Ç: ${color?.toLowerCase()}, —à–∏—Ä–∏–Ω–∞ ${width} –º,
                 —Å–∫–ª–∞–¥–∫–∞ ${waves}${cornice ? ', –ü—Ä–æ—Ñ–∏–ª—å–Ω—ã–π –∫–∞—Ä–Ω–∏–∑' : ''}.`
              }
              price={totalPrice}
              close={() => toggleModal(false)}
            />
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default TulleTab;
